.intro-container {
    display: flex;
    flex-direction: row;
    color: white;
    padding: 12rem 12rem 20rem 12rem;
    background-color: var(--raisin-black);
}

.intro-words {
    display: flex;
    flex-direction: column;
    padding-top: 1.2rem;
}

.intro-words h1 {
    font-weight: 600;
    font-size: clamp(40px, 8vw, 80px);
    border-bottom: 1px solid var(--pacific-blue);
    padding-bottom: .5rem;
    width: 50%;
}

.intro-words h2 {
    font-weight: 400;
    font-size: clamp(20px, 4vw, 40px);
    padding: .5rem 0;
}

.intro-words h3 {
    font-size: clamp(20px, 2vw, 20px);
}

.intro-words h4 {
    padding-top: .5rem;
    font-weight: 400;
    font-size: clamp(10px, 2vw, 20px);
}

.carousel {
    width: 20rem;
    align-self: center;
    margin-right: 4rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
}

.carousel p {
    display: none;
}

.photo {
    border-radius: 50%;
    -webkit-border-radius: 50%;
}

.photo img {
    border-radius: 50%;
    -webkit-border-radius: 50%;
}

.thumbs-wrapper {
    display: none;
}

@media screen and (max-width: 980px) {
    .intro-container {
        padding: 12rem 4rem 20rem 4rem;
    }

    .intro-words h1 {
        width: 70%;
    }

    .carousel {
        width: 15rem;
        margin-right: 4rem;
    }
}

@media screen and (max-width: 575px) {
    .intro-container {
        padding: 5rem 2rem 5rem 2rem;
    }
    .intro-words h1 {
        width: 75%;
    }
    .carousel {
        width: 12rem;
        margin-right: 2rem;
        margin-top: 2rem;
    }
    .intro-words h4 {
        font-size: clamp(14px, 2vw, 20px);
    }
}

@media screen and (max-width: 575px) {
    .intro-container {
        flex-direction: column;
        padding: 1rem 2rem 5rem 2rem;
    }
    .carousel {
        margin-left: auto;
        margin-right: auto;
    }
}